<script>
import HomeComponent from '@/components/HomeComponent.vue'
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Tooltip,
  Legend,
  Title
} from 'chart.js'
import * as chConfig from './game/chartConfig.js'
import { Line } from 'vue-chartjs'
import {toRaw} from "vue";
import FactsComponent from '@/components/FactsCopmonent.vue'
ChartJS.register(CategoryScale, LinearScale, PointElement, LineElement, Tooltip, Legend, Title)
export default {
  name: 'HomeView',
  components: {
    Line,
    HomeComponent,
    //FactsComponent
  },
  computed: {
    mtLgClasses () {
      if (this.width > 1400) {
        return "mt-lg-5";
      }
      return "";
    },
    desktopClasses () {
      if (this.width <= 1200 && this.width >= 720) {
        return "container d-lg-none d-md-none d-sm-none d-none";
      }
      return "container d-lg-block d-md-block d-sm-none d-none"
    },
    tabletAndMobileClasses () {
      if (this.width <= 1200 && this.width >= 720) {
        return "container";
      }
      return "container d-lg-none d-md-none d-sm-block d-block"
    },
    classesBottom () {
      if (this.width <= 1200) {
        return "col-lg-6 col-md-12 col-sm-12 col-xs-12 font-18";
      }
      return "col-lg-6 col-md-6 col-sm-12 col-xs-12 font-18"
    },
    chartData: {
      get() {
        return {
          ticks: [{fontColor: '#FFFFFF'}],
          datasets: this.dsets,
          labels: Array(chConfig.options.scales.x.suggestedMax).fill().map((e, i) => i+1)
        }
      },
      set (value) {
        this.dsets = value
      }
    },
    chartOptions() {
      let options = JSON.parse(JSON.stringify(chConfig.options));
      options.plugins.legend.display = true
      options.plugins.title.display = true
      options.plugins.title.text = 'Прибыль всех кейсов'
      options.scales.y.title.text = ['Заработанная прибыль', 'накопленным итогом, $']
      return options
    },
    myStyles () { return chConfig.styles },
    chartDataPr: {
      get() {
        return {
          ticks: [{fontColor: '#FFFFFF'}],
          datasets: this.dsetsPr,
          labels: Array(chConfig.options.scales.x.suggestedMax).fill().map((e, i) => i+1)
        }
      },
      set (value) {
        this.dsetsPr = value
      }
    },
    chartOptionsPr() {
      let options = JSON.parse(JSON.stringify(chConfig.options));
      options.plugins.legend.display = true
      options.plugins.title.display = true
      options.plugins.title.text = 'Доходность всех кейсов'
      options.scales.y.title.text = 'Доходность, %'
      return options
    },
    myStylesPr () { return chConfig.styles },
    chartDataRisk: {
      get() {
        return {
          ticks: [{fontColor: '#FFFFFF'}],
          datasets: this.dsetsRisk,
          labels: Array(chConfig.options.scales.x.suggestedMax).fill().map((e, i) => i + 1)
        }
      },
      set (value) {
        this.dsetsRisk = value
      }
    },
    chartOptionsRisk() {
      let options = JSON.parse(JSON.stringify(chConfig.options));
      options.plugins.legend.display = true
      options.plugins.title.display = true
      options.plugins.title.text = 'Риски всех кейсов'
      options.scales.y.title.text = 'Риск каждой сделки, % '
      return options
    },
    myStylesRisk () { return chConfig.styles }
  },
  data() {
    return {
      startMoney: [],
      totalSum: {},
      dsets: [],
      dsetsPr: [],
      dsetsRisk: [],
      width: window.innerWidth,
      profitability: { 1: 0, 2: 0, 3: 0},
      averageRisk: { 1: 0, 2: 0, 3: 0},
    }
  },
  methods: {
    updateWidth() {
      this.width = window.innerWidth;
    },
    getProfit(start, finish) {
      let res =  finish - start
      if (!Number.isInteger(res))
        return res.toFixed(0)
      return res
    },
    getProfitability(start, finish) {
      let res =  (finish / start) * 100
      if (!Number.isInteger(res))
        return res.toFixed(0)
      return res
    },
    getRisk (start, finish) {
      let res = Math.abs(finish - start) / start * 100
      if (!Number.isInteger(res))
        return res.toFixed(0)
      return res
    },
    toBottom(){
      window.scrollTo(0,
          document.body.scrollHeight || document.documentElement.scrollHeight);
    }
  },
  mounted() {
    window.addEventListener('resize', this.updateWidth);
    if (localStorage.totalSum) {
      this.totalSum = JSON.parse(localStorage.totalSum)
    }
    if (localStorage.historyPoints) {
      let points = JSON.parse(localStorage.historyPoints)
      let dsets = []
      let dsetsPr = []
      let dsetsRisk = []
      for (let i = 1; i <= 3; i++) {
        let chartColors = '#'+(0x1000000+Math.random()*0xffffff).toString(16).substr(1,6)
        points[i][0].backgroundColor = chartColors
        points[i][0].borderColor = chartColors
        points[i][0].label = "Кейс " + i
        this.startMoney[i] = points[i][0].data[0].y
        let data = [this.getProfit(points[i][0].data[0].y, points[i][0].data[1].y)]
        let dataPr = []
        let dataRisk = []
        this.averageRisk[i] = 0
        this.profitability[i] = 0
        for (let j = 1; j < points[i][0].data.length; j++){
            if(j > 1) {
              data.push(
                  this.getProfit(
                      points[i][0].data[j - 1].y, points[i][0].data[j].y) + data[data.length - 1]
              )
            }
            dataPr.push(
                this.getProfitability(
                    this.startMoney[i],  Number(data[j-1])
                )
            )
            dataRisk.push(
                this.getRisk(
                    points[i][0].data[j - 1].y, points[i][0].data[j].y
                )
            )
            this.averageRisk[i] += Number(dataRisk[dataRisk.length - 1])
        }
        dsets.push(Object.assign({}, points[i][0]))
        dsetsPr.push(Object.assign({}, points[i][0]))
        dsetsRisk.push(Object.assign({}, points[i][0]))
        dsets[i-1].data = data
        dsetsPr[i-1].data = dataPr
        dsetsRisk[i-1].data = dataRisk
        this.averageRisk[i] /= 15
        this.profitability[i] =  Number(dataPr[dataPr.length - 1])
      }
      this.chartData = dsets
      this.chartDataPr = dsetsPr
      this.chartDataRisk = dsetsRisk
    }
  }
}
</script>

