import { Tooltip } from 'chart.js';
const thiscolor = "#000"
const thisfont = {
					size: 14,
					family: 'Georgia'
				}

export let options = {
					responsive: true,
					plugins: {
						legend: {
							display: false,
							color: thiscolor,
							font: thisfont,
							align: 'end'
						},
						title: {
							display: false,
							text: 'Графики всех кейсов',
							color: thiscolor,
							font: thisfont,
							align: 'end'
						},
						tooltip: {
							callbacks: {
								label: function(context) {
									let label = context.dataset.label || '';

									if (label) {
										label += ': ';
									}

									if (context.parsed.y !== null) {
										label += "$" + context.parsed.y.toLocaleString();
									}
									return label;
								}
							}
						}
					},
					scales: {
						x: {
							title: {
								display: true,
								text: "Номер сделки",
								color: thiscolor,
								font: thisfont
							},
							ticks: {
								color: thiscolor,
								font: thisfont,
								stepSize: 1,
							},
							grid: {
								color: thiscolor
							},
							suggestedMin: 0,
							suggestedMax: 15,
						},
						y: {
							title: {
								display: true,
								text: "Текущий счет, $",
								color: thiscolor,
								font: thisfont
							},
							ticks: {
								color: thiscolor,
								font: thisfont
							},
							grid: {
								color: thiscolor
							},
							suggestedMin: 0,
							suggestedMax: 10
						},

					}
				}

export const styles = {
					position: 'relative'
				}